<template>
	<view>
		<wybar @clickLeft="back"></wybar>
		<view class="tip">
			{{i18n.mine.editPhoneTip}}
		</view>
		
		<view class="inputback row">
			<view @click="chooseCountry()" style="width: 70px;height: 100%;align-items: center;position: relative;;" class="row">
				<view style="flex: 1;color: #000;font-size: 16px;">
					+{{countryCode}}
				</view>
				<image src="../../../static/xia.png" mode="aspectFit" style="width: 16px;height: 16px;margin-right: 18px;"></image>
				<view style="position: absolute;right: 9px;top: 11px;height: 18px;width: 1px;background-color: #D6DADE;">
					
				</view>
			</view>
			
			<input type="number" @input="inputphone" :value="mobile" class="input" style="margin-left: 15px;width: auto;">
		</view>
		
		<view class="inputback">
			<input type="text" @input="inputCode" :placeholder="i18n.login.placeCode" class="input">
		</view>

		<view class="save" @click="doSave()">
			{{i18n.mine.querenxiugai}}
		</view>
	</view>
</template>

<script>
	import wybar from '../../../components/wybar/wyBar.vue'
	export default {
		components: {
			wybar
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		
		data() {
			return {
				mobile:'',
				countryCode:'66'
			}
		},
		onShow() {
			var that = this
			uni.getStorage({
				key: 'countryCode',
				success: res => {
					that.countryCode = res.data
					console.log(res)
				}
			})
		},
		methods: {
			back() {
				uni.navigateBack({

				})
			},
			chooseCountry(){
				uni.navigateTo({
					url:'../../../pages/login/country'
				})
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #f5f6f7;
	}

	.tip {
		margin-left: 15px;
		width: calc(100% - 30px);
		margin-top: 30px;
		font-size: 24px;
		font-weight: bold;
		color: #7900B2;
	}

	.inputback {
		margin-left: 15px;
		width: calc(100% - 30px);
		margin-top: 20px;
		height: 60px;
		border-radius: 8px;
		background-color: #FFF;
	}

	.input {
		margin-left: 15px;
		height: 60px;
		line-height: 60px;
		font-size: 18px;
		font-weight: bold;
		color: #7900B2;
		width: calc(100% - 30px);
	}

	.save {
		left: 15px;
		width: calc(100% - 30px);
		height: 49px;
		line-height: 49px;
		border-radius: 8px;
		color: #FFF;
		text-align: center;
		font-size: 14px;
		font-weight: bold;
		background-color: #7900B2;
		position: absolute;
		bottom: 50px;
	}
</style>
